      link: 'https://www.meetup.com/ThoughtWorks-Muenchen/events/261831158/',
      description: '<p>AI is more than hype - it is here to stay. We are using AI-powered applications every day and companies are experimenting with this technology to find out how additional value can be created with AI. And although the possibilities are endless, they often figure out that the journey from running first promising proofs-of-concept to implementing AI-driven core applications is a difficult one. Becoming a data-driven or AI-driven company is more than building AI driven applications: It requires fundamental changes in how data and knowledge are managed, employees are working together, products are developed and the IT is organized.<br/>In this MeetUp Christoph Windheuser, Global Head of Artificial Intelligence at ThoughtWorks will talk about the current state of AI in the industry illustrated with several project examples.</p> <p>Christoph Windheuser is the Global Head of Artificial Intelligence at ThoughtWorks Inc. Before joining ThoughtWorks, he held several positions at SAP and Capgemini. Before that, he completed his PhD in Neural Networks with a focus on Speech Recognition.</p> <p>AGENDA:<br/>18:30 Snacks, drinks and mingle<br/>19:00 Talk and questions<br/>20:00 More questions, drinks and mingle</p> <p>SNACKS:<br/>As always we are offering various snacks and, beer and soft drinks. No full meal, but at least something to eat.</p> ',
