      link: 'https://www.meetup.com/munich-software-craft-community/events/262030083/',
      description: '<p>Uncle Bob\'s videos are the classics of the genre. They explain the craft, methods and reasons with absolute clarity. They are a great source of knowledge and motivation to become a cleaner coder. They are entertaining, perfect for the beginners but they also have enough ideas interesting for experienced programmers.</p> <p>The next sessions are dedicated to Design Patterns. We meet on the 27th of June to see episode 34 (<a href="https://cleancoders.com/episode/clean-code-episode-34/show" class="linkified">https://cleancoders.com/episode/clean-code-episode-34/show</a>). "You\'re going to love the discussion about the Bridge pattern. In it, I show you how to break down a subtype hierarchy with multiple degrees of freedom into a chain of several subtype hierarchies capturing one degree of freedom each.</p> <p>In the Chain of Responsibility discussion, we extend those hierarchies so that clients have no idea what server is serving them.</p> <p>Then we explore Greenspun\'s 10th Law by looking at the Interpreter pattern. And we learn that with great power comes great responsibility, and that most efforts to reduce programming workload by shifting it to the business simply cause that workload to bounce back to the programmers.</p> <p>You\'re going to find the Iterator discussion particularly interesting. It\'s not what you think it is. This discussion will teach you how to turn loops inside out so that the loops are at the bottom, being driven by the algorithm, instead of at the top and driving the algorithm.</p> <p>And then, at long last, the humble Adapter pattern. I\'ll go through a little history about comp.object discussions, table lamps, and interface naming. And then we\'ll explore the two primary forms of the Adapter. "</p> <p>This time the meetup is hosted and sponsored by Carl Zeiss Meditec AG (<a href="https://www.zeiss.de/meditec-ag/ueber-uns/standorte/deutschland-muenchen.html" class="linkified">https://www.zeiss.de/meditec-ag/ueber-uns/standorte/deutschland-muenchen.html</a>).</p> ',
