      link: 'https://www.meetup.com/Munchen-Drupal-User-Group-Meetup/events/tcdgfqyzjbjb/',
      description: '<p>Wir treffen uns einmal im Monat zu einem ungezwungen Coworking-Tag. Jeweils am ersten Donnerstag im Monat bietet uns dafür das Werk1 eine schöne Atmosphäre um uns in die Arbeit zu vertiefen aber auch nebenbei über Drupal-nahe und -fremde Themen zu sprechen.</p> <p>Format:<br/>An dem Tag soll für jeden die eigene Arbeit im Vordergrund stehen. Es soll aber gleichtzeitig dazu dienen einen Rahmen zu schaffen in dem man unkompliziert über konkrete Probleme oder interessante Ideen sprechen kann, oder einfach gemeinsam bei einem Kaffee über alltägliches, Webentwicklung und Drupal philosophieren kann.</p> <p>Zielgruppe:<br/>Jede und jeder Drupal-Enthusiast oder -Interessierte. Gerne auch Quereinsteiger, Hobbyisten, Entwickler, Designer, Frontender, Gründer oder Nutzer anderer Technoligie wie Wordpress, Typo3, Joomla, Symfony, React, Angular, ... . Hauptsache nette Leute.</p> <p>Räumlichkeiten:<br/>Im Coworking-Bereich "Communication Space" stehen Arbeitsplätze zur Verfügung. Im danebenliegenden Café mit ausreichend Sitzgelegenheiten können zudem Getränke und Speisen erworben werden.</p> <p>Kosten:<br/>Im Werk1 kann man für 20€ einen Tagespass fürs Coworking buchen. Die Abrechnung erfolgt direkt über das Werk1 vor Ort. Siehe <a href="https://www.werk1.com/coworking/" class="linkified">https://www.werk1.com/coworking/</a></p> ',
