      link: 'https://www.meetup.com/Flutter-Munich/events/260154719/',
      description: '<p>🏰Have you built something with Flutter? This is the time to show it to the world! This event is focussing on Flutter stories. You can share what you created with Flutter and how your experience was.</p> <p>🚀We would like to hear about your personal pet projects, your work for a client or your company or open source libraries. The talks should be a good mixture of Flutter, technology, your product or service, your team, an overview of the greatest challenges along the way and a good story. You can share with us why you used Flutter, how you used it and what you built as long as you can do it in 11 minutes.</p> <p>🎤Would you like to present? Please contact me if you, your company or your team is working on something awesome and using Flutter made shipping something great possible and you would like to share your story with us.</p> <p>🍻We are going to have 5+ short Flutter stories.</p> <p>👩\u200d🎓🕺👩\u200d💻This event will not focus heavily on coding, so feel free to invite your product owner, designer or entrepreneur friends. We have an attendee limit of 100 people. 💃👷💂\u200d♀️</p> <p>LOCATION</p> <p>📍The event\'s location: Isar Valley @ Google Munich</p> <p>PRESENTATIONS</p> <p>🎤 Stefan Schaller and Julian Bissekkou from Tikkr 🎤<br/>Tikkr is a time tracking tool for small startups and freelancers. You can create and track tasks. You always have an overview of your time and statistics.<br/>Tikkr is a team of three: Stefan, Julian (mobile developers) and Tobi (web developer). They also develop a web app in AngularDart, this enables them to share code between the web and mobile app.<br/><a href="https://tikkr.app" class="linkified">https://tikkr.app</a></p> <p>🎤Wolfgang Strunk from SV Pullach/iteratec 🎤<br/>Have you ever built an app for a non-profit organization? The SV Pullach App lets you figure out the next games, insert them to your calendar, search for match locations and get overall team information.<br/>A no longer maintained iOS App was the trigger for a rebuild of the outdated Android App. Flutter\'s cross-platform approach allowed to give it a try, backed by Wolfgang\'s personal 5 day budget for innovation projects.</p> <p>CONTRIBUTE:</p> <p>🗣️ If you\'d like to present, please read <a href="https://github.com/flutter-munich/built-with-flutter" class="linkified">https://github.com/flutter-munich/built-with-flutter</a></p> <p>🍕 Would you or your company like to host one of our upcoming events? Send me a message!</p> ',
      how_to_find_us: 'Isar Valley @ Google Munich. Get your badge at the lobby, we\'ll be there to show you where the event space is.',
      visibility: 'public'
