      group: {
        created: 1520252901000,
        name: 'Software, Technology & Design Meetup München',
        id: 27731423,
        join_mode: 'open',
        lat: 48.13999938964844,
        lon: 11.579999923706055,
        urlname: 'Software-Technology-Meetup-Munchen',
        who: 'Mitglieder',
        localized_location: 'München, Germany',
        state: '',
        country: 'de',
        region: 'en_US',
        timezone: 'Europe/Berlin'
      },
      link: 'https://www.meetup.com/Software-Technology-Meetup-Munchen/events/262067176/',
      description: '<p>Was wir unternehmen werden: Weiter geht’s in diesem Jahr mit spannenden Meetups in den Büroräumen unseres Sponsors medialesson in München! Dieses ist unser drittes Meetup in diesem Jahr und ersetzt das leider ausgefallene Meetup zum selben Thema vom Mai.</p> <p>Wir widmen uns heute einem Thema, das für viele Software-Entwickler, die sich mit der Gestaltung benutzerfreundlicher und innovativer Frontends befassen, sicherlich von großer Bedeutung ist: Wie kann ich mit Virtual, Augmented und Mixed Reality die User Experience verbessern und für mobile Anwendungen im Raum neuartige Darstellungsmethoden umsetzen?</p> <p>Und hier zu allererst: Welche Grundlagen und Tools gehören zur VR-/AR-App-Entwicklung? Dino Fejzagic, Mixed Reality-Entwickler bei der medialesson GmbH, stellt in seinem Vortrag die VR- und AR-App-Entwicklung in vielen ihrer Facetten vor und beschreibt, mit welchen Tools gute Ergebnisse erreicht werden können. Auch demonstriert Dino einige Mixed Reality-Apps in praktischen Beispielen. In einer anschließenden Diskussionsrunde, wollen wir uns gemeinsam der Frage widmen, wohin uns diese Reise noch führen kann.</p> <p>Dieses Meetup richtet sich an Software-Entwickler und IT-Architekten, die einen ersten Einstieg in die VR-/AR-Software-Entwicklung finden möchten.</p> <p>-.-.-</p> <p>Agenda</p> <p>18:00 Uhr - 18:30 Uhr: Kennenlernen bei Pizza und Kaltgetränken</p> <p>18:30 Uhr - 20:00 Uhr: Vortrag Dino Fejzagic mit praktischen Beispielen</p> <p>20:00 Uhr - 20:30 Uhr (oder länger, je nach Interesse): Offene Runde / Diskussion</p> <p>Falls jemand einen weiteren Vortrag zum Thema beisteuern möchte, wäre dies im Anschluss an den ersten Vortag möglich. Genügend Zeit für die Diskussionsrunde ist eingeplant. Meldet euch gerne einfach bei Dino!</p> <p>Catering: Pizza und Getränke werden auch diesmal von medialesson gesponsert. Vielen Dank dafür!</p> ',
