      link: 'https://www.meetup.com/Project-Management-Office-Stammtisch-Munchen-PMO-Meetup/events/258288737/',
      description: '<p>Die Ansprüche an ein Berichtswesen zu Projekten und Portfolios sind nicht zu unterschätzen. Was ist wirklich nötig? Weniger ist hier oft mehr. Schnell sind verantwortliche Personen mit der Aufgabenstellung, dem Aufwand und der Konsistenz der Informationen aber überfordern. Was Sie beachten sollten, um Berichte als Kernaufgabe des Projektmanagements möglichst einfach und effizient zu bewerkstelligen, darum geht es an diesem Abend.</p> <p>Nach einer kurzen Einführung sammeln die Teilnehmer in Arbeitsgruppen Ideen. Diese werden dann gemeinsam präsentiert und diskutiert. Tauschen Sie sich intensiv mit Ihren Peers aus und erweitern Sie Ihr Netzwerk. Jetzt gleich kostenlos anmelden.</p> <p>Achtung, die Plätze sind begrenzt. Bitte unbedingt vorher kostenlos anmelden unter <a href="https://www.theprojectgroup.com/pmo-stammtisch" class="linkified">https://www.theprojectgroup.com/pmo-stammtisch</a></p> <p>Und noch was: Werfen Sie unbedingt auch einen Blick auf das Programm unserer kostenlosen Webinare. Das Programm wird laufend aktualisiert: <a href="https://www.theprojectgroup.com/webinar" class="linkified">https://www.theprojectgroup.com/webinar</a></p> ',
