      description: '<p>⚡️.🎤</p> <p>Be a part of conversations that come alive with our growing community of the most diverse minds around.</p> <p>Is there a topic that you are passionate about? Working on a cool thesis? Or would you like to share an inspiring experience? That might spark up new ideas, feelings and curiosity?</p> <p>Hold a 5-minute ⏳ Lightning Talk ⚡ followed by a 5-minute Q&amp;A - To Inform, Inspire &amp; Ignite !</p> <p>***** ARE YOU IN? *****<br/>Apply for a spot as soon as possible via Email to [masked] with a short description of what you will talk about. 💌</p> <p>We\'ll join forces to prepare the talk along with a rehearsal where you\'d get to know the other speakers as well!</p> <p>Language: English (recommended) or German</p> <p>***** 𝗙𝗢𝗢𝗗 &amp; 𝗗𝗥𝗜𝗡𝗞𝗦 *****<br/>Beer, Longdrinks &amp; Snacks ( 3€ - 8€)</p> <p>***** 𝗚𝗢𝗢𝗗 𝗧𝗢 𝗞𝗡𝗢𝗪 *****<br/>All Talks will be recorded in order to capture this amazing event! After the intellectual overkill we head to a party or bar to continue the networking. 🍸 😎</p> <p>***** 𝗣𝗜𝗖𝗦 &amp; 𝗩𝗜𝗗𝗘𝗢𝗦 *****<br/><a href="https://www.facebook.com/LTMunich/" class="linkified">https://www.facebook.com/LTMunich/</a></p> <p><a href="https://www.youtube.com/channel/UCoiCuNxXXja8ig8HVJ6pBvw" class="embedded">https://www.youtube.com/channel/UCoiCuNxXXja8ig8HVJ6pBvw</a></p> ',
