      description: '<p>I’d like to invite all of you to our first-ever study jam/coding night. It’s going to be a very laid-back event, with great conversations, drinks, and food. The concept is simple: we meet and we build beautiful apps together.</p> <p>Flutter is Google’s portable UI toolkit to build beautiful native apps on iOS and Android from a single codebase. Flutter Study Jams are a great opportunity to get together to learn and practice this technology in person.</p> <p>👨\u200d💻We prepared a list of awesome tutorials, so you can create something on your own, all the while we are there to help you in case you get stuck.<br/>🐧You can also bring your own ideas or work on open-source projects.<br/>❓You can come with questions, we’ll do our best to help you with any issue you bring.<br/>🎤At the end of the event, you’ll have the opportunity to show to the rest of us what you worked on (presenting is optional, but encouraged).</p> <p>I highly recommend you complete the Flutter installation guide and the Test drive before the event. Installing everything (Xcode, Android Studio, emulators, simulators, enabling development mode on your phone) can take some time, especially if you are doing it the first time. You can get the most out of this event if you successfully run the Flutter example app (click on floating action button to increment counter) on either an emulator/simulator or on a physical device. If you have Flutter installed on your computer, check your version and upgrade if necessary.</p> <p>IMPORTANT</p> <p>💻You need to bring your own computer.<br/>📱Bring cables to connect your phone to your laptop, if you’d like to develop on a physical device (aka mobile phone).<br/>⚡Don’t forget your charger.<br/>👩\u200d💻Finish the Installation Guide before the event. <a href="https://flutter.dev/docs/get-started/install" class="linkified">https://flutter.dev/docs/get-started/install</a><br/>🏎Finish the Test drive before the event. <a href="https://flutter.dev/docs/get-started/test-drive" class="linkified">https://flutter.dev/docs/get-started/test-drive</a></p> <p>LOCATION</p> <p>📍The event\'s location, food, and drinks are provided by SinnerSchrader Deutschland GmbH</p> <p>CONTRIBUTE</p> <p>🗣️ If you would like to share your story with Flutter, contact me! I\'m already organizing the next events and we have slots for speakers.</p> <p>🍕 Would your company like to host one of our upcoming events? Send me a message!</p> ',
